import PendingView from '../../components/WalletModal/PendingView'
import SolanaWalletOptions from '../../components/WalletModal/SolanaWalletOptions'
import { useWallet } from '@solana/wallet-adapter-react'
import React, { useCallback, useState } from 'react'
import { useDispatch } from 'react-redux'
import { updateSolanaSelectedWallet } from '../../state/user/reducer'

const WALLET_VIEWS = {
  OPTIONS: 'options',
  ACCOUNT: 'account',
  PENDING: 'pending',
}

const SolanaWalletModal = () => {
  const [walletView, setWalletView] = useState(WALLET_VIEWS.OPTIONS)

  const { connect, select, connected, wallet } = useWallet()

  const dispatch = useDispatch()

  // useEffect(() => {
  //   if (wallet) {
  //     connect().catch(() => {})
  //   }
  // }, [wallet, connect]

  // useEffect(() => {
  //   wallet.adapter.addListener('error', (e) => {
  //     console.log(e)
  //   })
  // }, [wallet])

  const tryActivation = useCallback(
    async (walletName) => {
      try {
        // setPendingConnector(connector)
        setWalletView(WALLET_VIEWS.PENDING)
        // dispatch(updateConnectionError({ connectionType, error: undefined }))
        if (!connected) {
          select(walletName)
          await connect().catch((e) => console.log(e))
        }

        dispatch(updateSolanaSelectedWallet({ wallet: walletName }))
      } catch (error) {
        setWalletView(WALLET_VIEWS.OPTIONS)
        console.debug(`solana wallet connection error: ${error}`)
      }
    },
    [select, connect, connected, wallet],
  )

  const getModalContent = () => {
    if (walletView === WALLET_VIEWS.OPTIONS) {
      return <SolanaWalletOptions tryActivation={tryActivation} />
    }

    if (walletView === WALLET_VIEWS.PENDING) {
      return <PendingView goBack={() => setWalletView(WALLET_VIEWS.OPTIONS)} />
    }
  }
  return <div>{getModalContent()}</div>
}

export default SolanaWalletModal
