import React from 'react'
import {
  InstallPhantomOption,
  OpenPhantomWalletOption,
  PhantomWalletOption,
} from '../../components/WalletModal/PhantomWalletOption'
import { getHasPhantomExtensionInstalled, getIsSolanaInjected } from '../../connection/utils'
import useUserAgent from '../../utils/userAgent'
import { Box, Text } from '@chakra-ui/react'

const SolanaWalletOptions = ({ tryActivation }) => {
  const isMobile = useUserAgent()
  const getOptions = () => {
    const isSolanaInjected = getIsSolanaInjected()
    const hasPhantomExtension = getHasPhantomExtensionInstalled()

    let phantomOption
    if (!isSolanaInjected) {
      if (!isMobile) {
        phantomOption = <InstallPhantomOption />
      } else {
        phantomOption = <OpenPhantomWalletOption />
      }
    } else if (hasPhantomExtension) {
      phantomOption = <PhantomWalletOption tryActivation={tryActivation} />
    }

    return <>{phantomOption}</>
  }
  return (
    <Box bg="background">
      <Text color="foreground" fontSize={18} fontWeight={500}>
        Connect Wallet
      </Text>
      {getOptions()}
    </Box>
  )
}

export default SolanaWalletOptions
