import React, { useCallback, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import { Box, Button, Checkbox, Flex, Image, Text, useToast } from '@chakra-ui/react'
import Counter from '../../../components/Counter'
import useMint from './hooks/useMint'
import useFetchAbi from './hooks/useFetchAbi'
import { DEFAULT_TOKEN_DECIMAL } from '../../../constants'
import { BLOCK_EXPLORER, SupportedChainId } from '../../../constants/chains'
import { SUPPORTED_NETWORKS_NAMES } from '../../../constants/network'
import Loader from '../../../components/Loader'
import LoadingText from '../../../components/Loader/LoadingText'
import { useContract } from '../../../hooks/useContract'

import { LoadingScreen, StyledButton, StyledCloseIcon, StyledLink } from './styles'
import useMetaplex from '../../../hooks/useMetaplex'
import { useWallet } from '@solana/wallet-adapter-react'
import { updateMint, updateMintError } from '../../../state/collection/source'
import { useSelector } from 'react-redux'
import { Keypair, Transaction } from '@solana/web3.js'
import useSwap from '../Swap'
import BigNumber from 'bignumber.js'

export const truncateAddress = (address) => {
  return address.slice(0, 4) + '..' + address.slice(-4)
}

const Widget = ({ collectionState, cm, onClickClose, isCollectionFetching }) => {
  const [loading, setLoading] = useState(false)
  const [agreeTnc, setAgreeTnc] = useState(false)
  const [minted, setMinted] = useState(false)
  const [mintedTx, setMintedTx] = useState(null)
  const [payUsing, setPayUsing] = useState('BONK')
  const { account, provider } = useWeb3React()
  const { connected: solanaWalletConnected, publicKey, signAllTransactions, sendTransaction } = useWallet()

  const accessKey = useSelector((state) => state.user.accessKey)

  const toast = useToast()

  const {
    collectionId,
    contractAddress,
    chainId,
    mintPrice,
    mintPriceUnit,
    mintFunction,
    abi,
    isButtonDisabled,
    buttonTitle,
  } = collectionState
  const { isV3, metaplex, connection } = useMetaplex(chainId)

  const swapEnabled = !isCollectionFetching && mintPriceUnit === 'SOL'

  const {
    getQuote,
    getSwapTx,
    renderSwapDetails,
    requiredCustomAmount,
    isLoading: isFetchingSwapDetails,
  } = useSwap({
    toCurrency: payUsing,
    solAmountIn: mintPrice,
    swapEnabled,
  })

  const internalAbi = useFetchAbi({ contractAddress, chainId, abi })
  const contract = useContract(contractAddress, !!abi ? abi : internalAbi)

  const { isSolana, isWrongNetwork, requestNetworkSwitch, setQuantity, quantity, totalPrice } = useMint({
    mintPrice,
    chainId,
    minQuantity: 1,
  })

  const onClickCustomPay = (currency) => {
    setPayUsing(currency)
  }

  const onQuantityChange = (qty) => {
    setQuantity(qty)
  }
  const onQuantityError = () => {}

  const mint = async () => {
    try {
      setLoading(true)
      let needQuantity = true
      let needQuantityAndToAddress = false
      let args = []
      let transaction = null
      const priceInWei = totalPrice.times(quantity).times(DEFAULT_TOKEN_DECIMAL).toString()
      const parsedAbi = !!abi ? JSON.parse(abi) : JSON.parse(internalAbi)
      const _mintFunction = !!mintFunction ? mintFunction : 'mint'
      const abiFunction = parsedAbi.find(
        (item) => item.name === _mintFunction && item.type.toLowerCase() === 'function',
      )
      if (!!abiFunction) {
        if (abiFunction.inputs.length > 2) {
          throw new Error(`${_mintFunction} function is not supported`)
        }
        needQuantity = abiFunction.inputs.length === 1
        needQuantityAndToAddress = abiFunction.inputs.length === 2
        args = abiFunction.inputs.map((a) => (a.type === 'address' ? account : quantity))
      } else {
        throw new Error(`${_mintFunction} function is not available in contract`)
      }

      if (needQuantityAndToAddress) {
        transaction = await contract[_mintFunction](...args, {
          value: priceInWei,
        })
      } else if (needQuantity) {
        transaction = await contract[_mintFunction](quantity, {
          value: priceInWei,
        })
      } else {
        transaction = await contract[_mintFunction]({
          value: priceInWei,
        })
      }

      setMintedTx(transaction.hash)
      const receipt = await transaction.wait()
      const events = receipt.events.filter(({ event }) => event === 'Transfer')
      const tokens = events.map(({ args }) => args[2]?.toString())

      await updateMint(collectionId, accessKey, {
        walletAddress: account,
        transactionHash: transaction.hash,
        chainId,
        quantity,
        price: mintPrice,
        priceUnit: mintPriceUnit,
        tokens,
      })
      setMinted(true)
    } catch (e) {
      console.log(e)
      if (e.reason) {
        toast({
          title: `Failed to mint`,
          description: e.reason,
          status: 'error',
          duration: 5000,
        })
      } else {
        toast({
          title: `Failed to mint`,
          description: e.message,
          status: 'error',
          duration: 5000,
        })
      }

      const error = e?.reason ? e?.reason : e?.message

      const params = {
        errorLog: JSON.stringify({
          account,
          error,
        }),
      }

      if (!error.includes('user rejected transaction')) {
        updateMintError(collectionId, accessKey, params)
      }
    } finally {
      setLoading(false)
    }
  }

  const mintSolana = useCallback(async () => {
    try {
      if (!solanaWalletConnected) return

      setLoading(true)

      const recentBlockhash = await connection.getLatestBlockhash()

      let swapTx = null
      let mintTx = new Transaction({
        recentBlockhash: recentBlockhash?.blockhash,
      })

      const mintKeypair = Keypair.generate()

      if (payUsing !== 'default' && mintPriceUnit === 'SOL') {
        const _mintPrice = new BigNumber(requiredCustomAmount)
        const { transaction, signers } = await getSwapTx(_mintPrice)
        swapTx = transaction
        swapTx.feePayer = publicKey
        swapTx.recentBlockhash = recentBlockhash?.blockhash
        swapTx.partialSign(...signers)
        swapTx.verifySignatures(false)
      }
      const metaplexVersionMethod = cm && isV3(cm?.model) ? 'candyMachines' : 'candyMachinesV2'
      const mintMethod = cm && isV3(cm?.model) ? 'mint' : 'newMint'

      let mint = await metaplex[metaplexVersionMethod]()
        .builders()
        .mint({
          [mintMethod]: mintKeypair,
          candyMachine: cm,
          collectionUpdateAuthority: cm?.authorityAddress,
        })

      mintTx.add(
        mint.toTransaction({
          blockhash: recentBlockhash.blockhash,
          lastValidBlockHeight: recentBlockhash.lastValidBlockHeight,
        }),
      )

      mintTx.feePayer = publicKey
      mintTx.partialSign(mintKeypair)
      mintTx.verifySignatures(false)

      const txs = swapTx ? [swapTx, mintTx] : [mintTx]
      const signedTransactions = await signAllTransactions(txs)
      if (swapTx) {
        swapTx.verifySignatures(true)
      }
      mintTx.verifySignatures(true)

      let swapSignature = null
      if (swapTx) {
        swapSignature = await connection.sendRawTransaction(signedTransactions[0].serialize())
      }
      const mintSignature = await connection.sendRawTransaction(signedTransactions[!!swapTx ? 1 : 0].serialize())

      console.log(swapSignature)
      console.log(mintSignature)

      setMintedTx(mintSignature)
      setMinted(true)

      await updateMint(collectionId, accessKey, {
        walletAddress: publicKey.toBase58(),
        transactionHash: mintSignature,
        chainId,
        quantity,
        price: mintPrice,
        priceUnit: mintPriceUnit,
        // tokens: [mint.nft.address.toBase58()],
      })

      // getCandyMachine(new PublicKey(cmId))
      //     .then((_cm) => {
      //       setCm(_cm);
      //     })
      //     .catch((error) => {
      //       console.error(error);
      //     });
    } catch (e) {
      setLoading(false)
      console.log(e)
      toast({
        title: 'something went wrong.',
        description: e.message,
        status: 'error',
      })

      const error = e?.reason ? e?.reason : e?.message

      const params = {
        errorLog: JSON.stringify({
          account: publicKey.toBase58(),
          error,
        }),
      }

      if (!error.includes('User rejected the request')) {
        updateMintError(collectionId, accessKey, params)
      }
    } finally {
      setLoading(false)
    }
  }, [cm, isV3, metaplex, toast, solanaWalletConnected, payUsing, requiredCustomAmount])

  // const onClickClose = () => {
  //   setMinted(false)
  // }

  return (
    <Box mb={4} bg="background">
      {(loading || minted) && (
        <LoadingScreen>
          {minted && <StyledCloseIcon onClick={onClickClose} />}
          {minted ? (
            <Box>
              <Text color="foreground" fontWeight={600}>
                Minted Successfully!
              </Text>
            </Box>
          ) : (
            <>
              <Loader />
              <LoadingText color="foreground" fontWeight={600} text="Minting in Progress" mt={10} />
            </>
          )}
          {mintedTx && (
            <StyledLink
              target="_blank"
              href={`${BLOCK_EXPLORER[chainId]}tx/${mintedTx}${
                chainId === SupportedChainId.SOLANA_DEVNET ? '?cluster=devnet' : ''
              }`}
              style={{ color: '#1890ff !important' }}
            >
              View transaction
            </StyledLink>
          )}
        </LoadingScreen>
      )}
      <Flex flexDirection="column" alignItems="center">
        {/*<Text color="foreground" fontWeight={600} mb={'10px'}>*/}
        {/*  Ethereum*/}
        {/*</Text>*/}
        <Image
          src={
            collectionState?.profile?.profileImage
              ? `${collectionState?.profile?.profileImage}/height=150`
              : '/assets/images/BadBanditnft.png'
          }
          width={150}
          height={150}
          alt=""
          borderRadius="12"
        />
      </Flex>
      {isSolana && mintPriceUnit === 'SOL' && (
        <Flex justifyContent="space-between" mt={15}>
          <Text color="foreground" fontWeight={400} fontSize={14}>
            Pay using
          </Text>
          <Flex justifyContent="center">
            <Button
              colorScheme={payUsing === 'BONK' ? 'purple' : 'gray'}
              mr={5}
              size="xs"
              onClick={() => onClickCustomPay('BONK')}
            >
              BONK
            </Button>
            <Button
              colorScheme={payUsing === 'default' ? 'purple' : 'gray'}
              size="xs"
              onClick={() => onClickCustomPay('default')}
            >
              {mintPriceUnit ?? 'default'}
            </Button>
          </Flex>
        </Flex>
      )}

      <Flex justifyContent="space-between" mt={15}>
        <Text color="foreground" fontWeight={400} fontSize={14}>
          Price
        </Text>
        <Text color="foreground" fontWeight={600} fontSize={14}>
          {mintPrice} {mintPriceUnit}
        </Text>
      </Flex>
      {!isSolana && (
        <Flex justifyContent="space-between" mt={15} alignItems="center">
          <Text color="foreground" fontWeight={400} fontSize={14}>
            Quantity
          </Text>
          <Counter
            initialState={1}
            maxState={100}
            onChange={onQuantityChange}
            onError={onQuantityError}
            value={quantity}
          />
        </Flex>
      )}

      {payUsing !== 'default' && mintPriceUnit === 'SOL' && <Box>{renderSwapDetails()}</Box>}

      <Flex mt={4}>
        <Checkbox colorScheme="gray" isChecked={agreeTnc} onChange={() => setAgreeTnc(!agreeTnc)}>
          <Text fontSize={12}>
            I understand that I am interacting with a third party’s Smart Contract and accept the{' '}
            <a target="_blank" href="https://bandit.network/legal/termsOfUse.pdf" style={{ fontWeight: 600 }}>
              Terms of Use
            </a>
          </Text>
        </Checkbox>
      </Flex>

      <Box style={{ textAlign: 'center' }} mt={'15px'}>
        {!isSolana && isWrongNetwork ? (
          <Box>
            <Button color="buttonColor" bg="buttonBackground" scale="sm" onClick={requestNetworkSwitch}>
              Change network
            </Button>
            <Text color="foreground" fontSize="12px" textAlign="center" mb={'10px'} lineHeight={1} mt={2}>
              Wrong Network! Please change the network to {SUPPORTED_NETWORKS_NAMES[chainId]} network
            </Text>
          </Box>
        ) : (
          <>
            <StyledButton
              scale="sm"
              onClick={isSolana ? mintSolana : mint}
              varient="solid"
              colorScheme="gray"
              background="foreground"
              isDisabled={
                !agreeTnc || (isSolana && payUsing === 'default' && isButtonDisabled) || isFetchingSwapDetails
              }
              isLoading={isFetchingSwapDetails}
              loadingText={isFetchingSwapDetails ? 'Fetching best price' : ''}
            >
              <Box>
                <Text color="background" fontSize={12}>
                  {payUsing !== 'default' && mintPriceUnit === 'SOL'
                    ? `${requiredCustomAmount} ${payUsing}`
                    : `${totalPrice.toString()} ${mintPriceUnit}`}
                </Text>
                <Text color="background" textAlign="left" fontSize={'8px'} mt={'2px'}>
                  Total amount
                </Text>
              </Box>
              <Text color="background" ml={'20px'}>
                {isSolana && payUsing === 'default' ? buttonTitle : 'Mint'}
              </Text>
            </StyledButton>
          </>
        )}
      </Box>
    </Box>
  )
}

export default Widget
